import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormBuilder } from '@angular/forms';
import { ContactUIForm } from '../models/contact-ui-form';
import { ContactsServiceProvider } from '../../providers/contacts-service/contacts-service';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { LoginPage } from '../login/login';
import { CompaniesServiceProvider } from '../../providers/companies-service/companies-service';
import { Company } from '../models/company';
import { CommonListModal } from '../models/common-list-modal';
import { ContactFullDetailsPage } from '../contact-full-details/contact-full-details';


@IonicPage()
@Component({
  selector: 'page-contact-view-details',
  templateUrl: 'contact-view-details.html',
})
export class ContactViewDetailsPage {

  selectedContact: ContactUIForm;
  submitAttempt: boolean = false;
  contactUIForm: ContactUIForm= new ContactUIForm();
  
  public companiesModelList: Company[] = [];
  public designationsList: CommonListModal[] = [];

  companyNameString: string = "";

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public formBuilder: FormBuilder, 
    public contactsServiceProvider: ContactsServiceProvider,
    public authService: AuthenticationServiceProvider,
    public companiesServiceProvider: CompaniesServiceProvider,) {

    this.selectedContact = navParams.get('selectedContact');
    if(this.selectedContact) {
      this.contactUIForm = this.selectedContact;
      if(this.selectedContact.contactId) {
        this.getContactDetailsByData(this.selectedContact.contactId);
      }
    }

  }

  ionViewDidLoad() {
  }

  getContactDetailsByData(dataId) {
    // Show Loader
    this.authService.showLoaderFn('');
    // API Call
    this.contactsServiceProvider.getContactDetailsByData(dataId).subscribe(data => {

      if (data["statusCode"] === 0) {
        // Hide Loader
        this.authService.hideLoaderFn();
        this.contactUIForm = JSON.parse(data['response']) as ContactUIForm;

        this.companyNameString = this.contactUIForm.company.companyName;
      } else {
        this.authService.handleResponseStatusFn(data);
        if (data["statusCode"] === 2) {
          this.navCtrl.setRoot(LoginPage);
        }
      }
    }, err => {
      // Hide Loader and Show Message
      this.authService.handleErrorFn(err);
      if (err.status === 401) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
  }
  
  editContactDetails(selectedContact) {
    this.navCtrl.push(ContactFullDetailsPage, {
      selectedContact: selectedContact
    });
  }

}




