import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { environment } from '../../environments/environment';
import { AuthenticationServiceProvider } from '../authentication-service/authentication-service';
import { ContactUIForm } from '../../pages/models/contact-ui-form';

@Injectable()
export class ContactsServiceProvider {

  public static readonly GET_ALL_DETAILS = environment.apiUrl + '/mapp/api/contacts/all';
  public static readonly SAVE_CONTACT = environment.apiUrl + '/mapp/api/contacts/save';
  public static readonly SAVE_CONTACT_GET = environment.apiUrl + '/mapp/api/contacts/saveGet';

  public static readonly GET_DETAILS = environment.apiUrl + '/mapp/api/contacts/get';
  public static readonly GET_ALL_DESIGNATIONS = environment.apiUrl + '/mapp/api/designation/all';
  

  constructor(public http: HttpClient, public authService: AuthenticationServiceProvider) {
    // console.log('Hello ContactsServiceProvider Provider');
  }

  getAll() {
    const userToken = this.authService.getUserToken();
    const urlToCall = ContactsServiceProvider.GET_ALL_DETAILS + '/' + userToken + '.do';

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
  }

  getContactDetailsByData(dataId) {
    const userToken = this.authService.getUserToken();
    const urlToCall = ContactsServiceProvider.GET_DETAILS 
    + '/' + dataId 
    + '/' + userToken 
    + '.do';

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
    // .catch(this.handleError);

  }

  getAllDesignationsAPI() {
    const userToken = this.authService.getUserToken();
    const urlToCall = ContactsServiceProvider.GET_ALL_DESIGNATIONS + '/' + userToken + '.do';
    return this.http.get(urlToCall)
    .map(res => <any[]>res);
  }

  saveContact(contactUIForm :ContactUIForm) {
    const userToken = this.authService.getUserToken();
    contactUIForm.userToken = userToken;
    
    const urlToCallGet = ContactsServiceProvider.SAVE_CONTACT_GET 
    +  '/' + JSON.stringify(contactUIForm)
    + '.do';
    return this.http.get(urlToCallGet)
    .map(res => <any>res);

    // const urlToCall = ContactsServiceProvider.SAVE_CONTACT + '.do';

    // let headers = new Headers();
    // headers.append('Content-Type', 'application/json');
    // let options = new RequestOptions({ headers: headers });

    // set('Authorization', 'Bearer ' + this.getAuthAccessToken())
    // return this.http.post(urlToCall, JSON.stringify(contactUIForm), {
      // headers: new HttpHeaders().set('Content-Type', 'application/json') 
    // })
    // .map(res => res);
  }

}



